import { 
  login,
  logout,
  register,
  get_email_code,
  valid_email,
  valid_email_status,
  reset_pwd,

  get_random_apikey,
  symbols,
  add_apikey_secretkey,
  fund_transfer, 
  transfer_out,
  query_single_wallet,
  deposit_address,
  history_deposity,
  query_transfer_records,
  get_depth,recent_trade,
  exchange_info,balance,
  trade_limit,trade_cancel,
  query_order,
  query_open_order,
  query_recent_complete_order,  
  query_recent_trade 
} from '@/api/userApi'
import { getToken, setToken, removeToken } from '@/utils/auth'

const user = {
  state: {
    token: getToken(),                                   // token值
    users: JSON.parse(localStorage.getItem('users')),    // 用户信息
  },

  mutations: {
    SET_TOKEN:(state, token) => {
      state.token = token
    },
    SET_USERS:(state, users) => {
      state.users = users
    }
  },

  actions: {
    // 登录
    login({commit}, loginInfo) {
      return new Promise((resolve, reject) => {
        login(loginInfo).then(res => {
          if(res.data.result == 0){
            commit('SET_TOKEN', res.data.token)
            commit('SET_USERS', res.data.user)
            setToken(res.data.token)
            localStorage.setItem('users', JSON.stringify(res.data.user))
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },

    // 注册
    register({commit}, registerInfo) {
      return new Promise((resolve, reject) => {
        register(registerInfo).then(res => {
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },

    // 退出
    logout({commit}, logoutInfo) {
      return new Promise((resolve, reject) => {
        logout(logoutInfo).then(res => {
          if(res.data.result == 0){
            commit('SET_TOKEN', '')
            commit('SET_USERS', '')
            removeToken()
            localStorage.clear()
            resolve(res)
          }
        }).catch(error => {
          reject(error)
        })
      })
    },

    // 获取邮箱验证码
    get_email_code({commit},data){
      return new Promise((resolve, reject) => {
        get_email_code(data).then(res => {
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },

    // 验证邮箱正确性
    valid_email({commit},data){
      return new Promise((resolve, reject) => {
        valid_email(data).then(res => {
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },

    // 验证邮箱是否成功
    valid_email_status({commit},data){
      return new Promise((resolve, reject) => {
        valid_email_status(data).then(res => {
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },

    // 重置密码
    reset_pwd({commit},data){
      return new Promise((resolve, reject) => {
        reset_pwd(data).then(res => {
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    }

    // // 活动委托取消订单
    // cancelOrder({commit}, cancelOrderInfo){
    //   return new Promise((resolve, reject) => {
    //     cancelOrder(cancelOrderInfo).then(response => {
    //       if(response){
    //         resolve(response)
    //       }
    //     }).catch(error => {
    //       reject(error)
    //     })
    //   })
    // },
  }
}

export default user



