
<template>
  <div class="tv_chart_container" id="tv_chart_container" />
</template>

<script>
    import { widget } from '../../../static/charting_library.min'
    // console.log('widget', widget)
    import Datafeed from './KS/'

    function getLanguageFromURL() {
        const regex = new RegExp('[\\?&]lang=([^&#]*)');
        const results = regex.exec(window.location.search);
        return results === null ? null : decodeURIComponent(results[1].replace(/\+/g, ' '));
    }

    export default {
        name: 'Ks',
        inject:['reload'],
        props: {
            symbol: {
                required: true,
                type: String,
            },
            interval: {
                default: 'D',
                type: String,
            },
            containerId: {
                default: 'tv_chart_container',
                type: String,
            },
            datafeedUrl: {
                default: 'https://127.0.0.1',
                type: String,
            },
            libraryPath: {
                default: 'static/charting_library-master/charting_library/',
                type: String,
            },
            chartsStorageUrl: {
                default: 'https://127.0.0.1',
                type: String,
            },
            chartsStorageApiVersion: {
                default: '1.1',
                type: String,
            },
            clientId: {
                default: 'tradingview.com',
                type: String,
            },
            userId: {
                default: 'public_user_id',
                type: String,
            },
            fullscreen: {
                default: false,
                type: Boolean,
            },
            autosize: {
                default: true,
                type: Boolean,
            },
            studiesOverrides: {
                type: Object,
            },
        },
        data:function(){
            return {
                tvWidget: null,
            }
        },
        methods: {},
        created() {
            
        },
        mounted() {
            // console.log(this.symbol)
            var _this = this;
            this.$nextTick(()=>{
                let widget = new TradingView.widget({
                    "symbol":this.symbol,
                    "datafeed": Datafeed,
                    "interval": this.interval,
                    "container_id": "tv_chart_container",
                    "library_path": this.libraryPath,
                    "locale": "zh",
                    "disabled_features": [
                        'use_localstorage_for_settings',
                        "header_symbol_search",
                        "header_settings",
                        "header_saveload",
                        "header_indicators",
                        "header_compare",
                        // "header_undo_redo",
                        // "header_screenshot",

                        "timeframes_toolbar",

                    ],
                    'enabled_features': [
                        'study_templates',
                    ],

                    'charts_storage_url': this.chartsStorageUrl,
                    'charts_storage_api_version': this.chartsStorageApiVersion,
                    'client_id': this.clientId,
                    'user_id': this.userId,

                    "fullscreen": this.fullscreen,
                    "theme": 'Dark',
                    "toolbar_bg": "#191B2A",
                    "autosize": true,
                    "charts_storage_api_version": "1.1",
                    "client_id": 'tradingview.com',
                    "user_id": 'public_user_id',
                    "timeframe":'',
                    "withdateranges":false,
                    // "saved_data": _this.savedData,
                    "allow_symbol_change": false,
                    
                    "overrides": {
                        "symbolWatermarkProperties.color": "rgba(0,0,0,0)",
                        
                        /* 图表的属性设置 */
                        "paneProperties.background": "#131722",
                        "paneProperties.vertGridProperties.color": "#363C4E",
                        "paneProperties.horzGridProperties.color": "#363C4E",
                        "paneProperties.crossHairProperties.color": "#58637a",
                        "paneProperties.crossHairProperties.style": 2,
                        
                    },
                    "timezone":'Asia/Shanghai',
                    "custom_css_url": 'chart.css',

                    "customFormatters": {
                        timeFormatter: {
                            format: function(date) { var _format_str = '%h:%m'; return _format_str.replace('%h', date.getUTCHours(), 2). replace('%m', date.getUTCMinutes(), 2). replace('%s', date.getUTCSeconds(), 2); }
                        },
                        dateFormatter: {
                            format: function(date) { return date.getUTCFullYear() + '-' + date.getUTCMonth() + '-' + date.getUTCDate(); }
                        }
                    },
                    "favorites": {
                        chartTypes: ["Area", "Line"]
                     },
                });
                _this.tvWidget = widget

                widget.onChartReady(() => {
                    widget.headerReady().then(() => {
                        // const button = tvWidget.createButton();

                        // button.setAttribute('title', 'Click to show a notification popup');
                        // button.classList.add('apply-common-tooltip');

                        // button.addEventListener('click', () => tvWidget.showNoticeDialog({
                        //     title: 'Notification',
                        //     body: 'TradingView Charting Library API works correctly',
                        //     callback: () => {
                        //     console.log('Noticed!');
                        //     },
                        // }));

                        // button.innerHTML = 'Check API';
                    });
                });
            })
        },
        
        destroyed() {
            if (this.tvWidget !== null) {
                this.tvWidget.remove();
                this.tvWidget = null;
            }
        }
    }
</script>
<style lang="scss" scoped>
    .tv_chart_container{
        height: 451px;
        min-height: 451px;
    }
</style>


