import { 
    get_single_rule,
    limit_opera,
    cancel_order,
    market_opera,
    funds_list,
    charge_info,
    mention_info,
    financial_record,
    mention,
    men_his_list,
    add_menaddress,
    coinGet,
    delete_menaddress,
    getLinkName,
    historyKs,
    fundsTotal
} from '@/api/otherApi'

const other = {
    actions: {
        // 单条规则
        get_single_rule({commit}, data){
            return new Promise((resolve,reject) => {
                get_single_rule(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 限价操作
        limit_opera({commit}, data){
            return new Promise((resolve,reject) => {
                limit_opera(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 市价操作
        market_opera({commit}, data){
            return new Promise((resolve,reject) => {
                market_opera(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 撤单
        cancel_order({commit}, data){
            return new Promise((resolve,reject) => {
                cancel_order(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 资产列表
        funds_list({commit}, data){
            return new Promise((resolve,reject) => {
                funds_list(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 充币信息
        charge_info({commit}, data){
            return new Promise((resolve,reject) => {
                charge_info(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 提币信息
        mention_info({commit}, data){
            return new Promise((resolve,reject) => {
                mention_info(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 提币
        mention({commit}, data){
            return new Promise((resolve,reject) => {
                mention(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 财务记录
        financial_record({commit}, data){
            return new Promise((resolve,reject) => {
                financial_record(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 提币历史列表
        men_his_list({commit}, data){
            return new Promise((resolve,reject) => {
                men_his_list(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 添加提币地址
        add_menaddress({commit}, data){
            return new Promise((resolve,reject) => {
                add_menaddress(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 币种获取
        coinGet({commit}, data){
            return new Promise((resolve,reject) => {
                coinGet(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 链名称获取
        getLinkName({commit}, data){
            return new Promise((resolve,reject) => {
                getLinkName(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 删除提币地址
        delete_menaddress({commit}, data){
            return new Promise((resolve,reject) => {
                delete_menaddress(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 历史K线数据
        historyKs({commit}, data){
            return new Promise((resolve, reject) => {
                historyKs(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        },

        // 资产折合总产
        fundsTotal({commit}, data){
            return new Promise((resolve, reject) => {
                fundsTotal(data).then(res => {
                    resolve(res)
                }).catch(err => {
                    reject(err)
                })
            })
        }
    }
}

export default other


