<template>
  <div class="account_center_wrapper">
    <div class="account_center_content">
      <iframe :src="src" width="100%" height="100%" frameborder="0" scrolling="auto" ></iframe>
    </div>  
  </div>
</template>

<script>
  export default {
   data() {
     return {
       src:''
     }
   },
   created(){
     this.handleSrc()
   },
   methods: {
    handleSrc(){
      var srcObj = JSON.parse(localStorage.getItem('srcObj'))
      if(srcObj){
        this.src = 'http://pay.fricobloc.com/acq_index/' + srcObj.show
      }else{
        this.src = 'http://pay.fricobloc.com/acq_index/1'
      }
    }
   },
   components: {

   }
  }
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  .account_center_content{
    background-color: #fff;
    color: #000;
    height: 100%;
  }
</style>



