import http from '@/utils/http'
import qs from 'qs'
import CryptoJS from 'crypto-js'
import {getToken} from '@/utils/auth'

function webSign(token, message){
  const key = qs.stringify(token) + "123456"
  const hash = CryptoJS.HmacSHA256(message, key);
  return CryptoJS.enc.Base64.stringify(hash);
}

function getSign( timestamp, method, request_path , body , token){
  const message = timestamp + method + request_path + body
  return webSign(token, message)
}

function makeRequest( method , params , request_path , token ){
  if(method === 'POST') {
    const timestamp = new Date().getTime() / 1000.0
    const str_data = qs.stringify(params)
    const sign = getSign(timestamp, method,  request_path , str_data)
    const headers = {
      "SC-ACCESS-FROM":"WEB",
      "SC-ACCESS-TIMESTAMP":timestamp,
      "SC-SESSION-AES":qs.stringify(token),
      "SC-ACCESS-SIGN":sign
    }
    return http({
      url:request_path + "?" + str_data,
      method:'post',
      headers: headers
    })
  }else{
    return http({
      url:request_path,
      method:'get',
      params: params
    })
  }
}

