import Vue from 'vue'
import App from './App'

// import 'normalize.css/normalize.css'                        // A modern alternative to CSS resets

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import locale from 'element-ui/lib/locale/lang/en'         // 国际化
Vue.use(ElementUI, {locale })
import i18n from '@/i18n/i18n'

import '@/style/index.scss'     // global css

import store from './store'
import router from './router'
import anchorFunc from '@/utils/anchor'

Vue.directive('anchor', anchorFunc)                       // 自定义指令

import './permission'                                     // permission control

import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)

import VueLoading from 'vue-loading-template'
Vue.use(VueLoading)
Vue.prototype.bus = new Vue;

import * as socketApi from '@/utils/socket'
Vue.prototype.socketApi = socketApi

import * as filters from '@/filters/filter'                // 导入所有的过滤器变量
Object.keys(filters).forEach(key => {                      // 导出的是对象，可以直接通过key和value来获得过滤器的名和过滤器的方法
  Vue.filter(key, filters[key])
})

Vue.config.productionTip = false

import axios from 'axios'

Vue.prototype.$http = axios;

var vue = new Vue({
  el: '#app',
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')

export default vue



