var websock = null;
var global_callback = null;
var serverPort = '8888';	//webSocket连接端口
var interval
var datanews = []

function getWebIP(){
	var curIP = '119.3.65.40';
	return curIP;
}

//初始化weosocket
function initWebSocket(){

    //判断当前浏览器是否支持WebSocket
    if ('WebSocket' in window) {
      var wsurl = "ws://" +getWebIP()+ ":" + serverPort;
      websock = new WebSocket(wsurl);
    }else if ('MozWebSocket' in window) {
      var wsurl = "ws://" +getWebIP()+ ":" + serverPort;
      websock = new WebSocket(wsurl);
    }
    else {
      interval = window.setInterval(function(){ //每隔30秒钟发送一次心跳，避免websocket连接因超时而自动断开
        // forgeWebSocket这个方法是在不支持websocket的浏览器中去执行
        this.forgeWebSocket();
      },30000);
    }

    //连接成功建立的回调方法
    websock.onopen = function (e) {
      websocketOpen(e);
	  }

    //接收到消息的回调方法
    websock.onmessage = function(e){
      // console.log(e)
    	websocketonmessage(e);
    }

    //连接关闭的回调方法
    websock.onclose = function(e){
      // websocketclose(0,e);
      // console.log('关闭回调e', e)
      if(e.code == 1006){
        websocketclose(0)
      }
    }

    //连接发生错误的回调方法
    websock.onerror = function (e) {
      websocketerror(e)
    }
}

//数据接收
function websocketonmessage(e){
  if(global_callback){
    global_callback(JSON.parse(e.data));
  }

  var receiveData = JSON.parse(e.data)

  if(receiveData.type == 'ping'){
    var sendData = {"type":"pong"}
    websock.send(JSON.stringify(sendData));
  }
}

// 数据发送
function websocketsend(agentData){
  websock.send(JSON.stringify(agentData));
}

// 关闭
function websocketclose(type=0){
  if(type == 1){
    // console.log('手动关闭连接，不用再连接',type)
    websock.close()
    datanews = []
  }else{
    initWebSocket()
  }
}

// 成功
function websocketOpen(e){
  // console.log("连接成功，未发送",e);
  if(datanews.length>0){
    // console.log("连接成功, 开始发送");
    for(var i =0 ;i < datanews.length;i++){
      // console.log('datanews[i]连接成功', datanews[i])
      websocketsend(datanews[i])
    }
  }
}

// 失败
function websocketerror(e){
  // console.log("连接失败",e);
  websocketclose(1)
}

//  readyState表示连接有四种状态：
//  CONNECTING (0)：表示还没建立连接；
//  OPEN (1)： 已经建立连接，可以进行通讯；
//  CLOSING (2)：通过关闭握手，正在关闭连接；
//  CLOSED (3)：连接已经关闭或无法打开

// 实际调用的方法
function sendSock(type,agentData,callback=''){
    // datanews = agentData
    if(callback){
      global_callback = callback;
    }
    // datanews = agentData
    if(type == 1){
        if (websock.readyState === websock.OPEN) {
            if(agentData){
              if(datanews.length>0){
                var isHas = false
                for(var i = 0;i<datanews.length;i++){
                  if(datanews[i].type == agentData.type){
                    datanews[i] = agentData
                    isHas = true
                  }
                }
                if(!isHas){
                  datanews.push(agentData)
                }else{
                  isHas = false
                }
              }else{
                datanews.push(agentData)
              }
            }
            //若是ws开启状态
            websocketsend(agentData)

        }else if (websock.readyState === websock.CONNECTING) {
            // 若是 正在开启状态，则等待1s后重新调用
            setTimeout(function () {
                sendSock(1,agentData,callback);
            }, 1000);
        }else {
          initWebSocket();

          setTimeout(function () {
              sendSock(1,agentData,callback);
          }, 1000);
            // 若未开启 ，则等待1s后重新调用
            // setTimeout(function () {
            //     initWebSocket();
            //     // sendSock(1,agentData,callback);
            // }, 1000);
        }
    }else if(type  == 2){
      websocketclose(1)
    }
}


initWebSocket();

export{sendSock}



