import M from '@/main'

const history = {}

export default {
    history: history,

    getBars:function(symbolInfo, resolution, from, to, first, limit){
		const period = resolution === 'D'?'d1':resolution === '1D'?'d1':resolution>=60?'h1':'m1'
        const data = {
			'symbol':symbolInfo.name.split('/').join('_'),
			"type":"SPOT",
			"start_date":this.fullDate(from),
			"end_date":this.fullDate(to),
			"period":period
        }
        let p = new Promise((resolve, reject) => {
            M.$store.dispatch('historyKs', data).then(res => {
                if(res.data.result == 0){
					if(res.data.i.length){
						// console.log(`Actually returned: ${new Date(data.TimeFrom * 1000).toISOString()} - ${new Date(data.TimeTo * 1000).toISOString()}`)
						var bars = res.data.i.map(el => {
							return {
								time:el.time * 1000,
								low:el.low,
								high:el.high,
								open:el.open,
								close:el.close,
								volume:el.volume
							}
						})
						// console.log('bars',bars)
						if(first){
							var lastBar = bars[bars.length - 1]
							history[symbolInfo.name] = {lastBar: lastBar}
						}

						resolve(bars)
					}else{
						var bars = []
						resolve(bars)
					}
				}else{
					var bars = []
					resolve(bars)
				}
            }).catch(err => {
                var bars = []
				reject(bars)
            })
        })
        return p
	},
	
	fullDate:function(date){
		if(date){
			if(String(date).length == 10){
			  date = date + '000'
			}
			var date = new Date(parseFloat(date))
			const Y = date.getFullYear() + '-';
			const M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
			const D = (date.getDate() < 10 ? '0'+date.getDate() : date.getDate()) + ' ';
			const h = (date.getHours() < 10 ? '0'+date.getHours() : date.getHours()) + ':';
			const m = (date.getMinutes() < 10 ? '0'+date.getMinutes() : date.getMinutes()) + ':';
			const s = (date.getSeconds() < 10 ? '0'+date.getSeconds() : date.getSeconds());
			var ret = Y + M + D + h + m + s
			return ret
		  }else{
			return ''
		  }
	}
}


