<template>
  <div class="myAssetWrapper">
    <div class="my_asset_wrapper">
      <div class="my_asset_content">
        <h2 class="title">我的资产</h2>
        <p class="coin_sel">
          <el-dropdown trigger="click" @command="coins_sel">
            <span class="el-dropdown-link">
              {{commandVal || 'USD'}}估值<i class="el-icon-caret-bottom el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="BTC">BTC</el-dropdown-item>
              <el-dropdown-item command="USD">USD</el-dropdown-item>
              <el-dropdown-item command="CNY">CNY</el-dropdown-item>
              <el-dropdown-item command="JPY">JPY</el-dropdown-item>
              <el-dropdown-item command="KRW">KRW</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </p>
      </div>
      <div class="total_content">
        总资产净估值≈<span class="num">100.03</span>USD
      </div>
    </div>

    <div class="funds_account_wrapper">
      <div class="funds_account_title clearfix">
        <h2 class="title fl">资金账户</h2>
        <a href="javascript:;" class="fr acount_funds">
          关于资金账户
          <i class="el-icon-arrow-right"></i>
        </a>
      </div>
      <div class="funds_account_content">
        <p class="total_funds">总额</p>
        <p class="money">641.03<span class="coin_kinds">USD</span></p>
        <div class="btn_group">
          <el-button round>充币</el-button>
          <el-button round>提币</el-button>
          <el-button round>资金划转</el-button>
        </div>
      </div>
    </div>

    <div class="trade_account_wrapper">
      <div class="trade_account_content">

        <h2 class="title">交易账户</h2>
        <div class="trade_detail">
          <div class="trade_img">
            <div id="myChart" :style="{width: '300px', height: '200px'}"></div>
          </div>
          <div class="detail_info">
            <p class="total_name">总额</p>
            <p class="total_funds">100000.03</p>
            <p class="total_unit">USD</p>
          </div>
          <div class="trade_list">
            <div class="list_wrapper">
              <span class="list_circle" style="background:#B853EC;"></span>
              <span class="list_name">理财账户</span>
              <span class="list_funds">51.24USD</span>
              <a href="javascript:;" class="fr list_link">详情<i class="el-icon-arrow-right"></i></a>
            </div>
            <div class="list_wrapper">
              <span class="list_circle" style="background:#F26C62;"></span>
              <span class="list_name">法币账户</span>
              <span class="list_funds">51.24USD</span>
              <a href="javascript:;" class="fr list_link">详情<i class="el-icon-arrow-right"></i></a></a>

            </div>
            <div class="list_wrapper">
              <span class="list_circle" style="background:#F59D48;"></span>
              <span class="list_name">币币账户</span>
              <span class="list_funds">51.24USD</span>
              <a href="javascript:;" class="fr list_link">详情<i class="el-icon-arrow-right"></i></a></a>

            </div>
          </div>
        </div>

      </div>
    </div>

  </div>
</template>

<script>
  // 引入基本模板
  let echarts = require('echarts/lib/echarts')
  // 引入饼图组件
  require('echarts/lib/chart/pie');
  // 引入提示框和title组件
  // require('echarts/lib/component/tooltip')
  require('echarts/lib/component/title')
  export default {
   data() {
     return {
      commandVal:''
     }
   },
   methods: {
    /* 币种选择 */
    coins_sel(command){
      this.commandVal = command
    },
    /* 
     饼图绘制
    */
    drawLine(){
      // 基于准备好的dom，初始化echarts实例
      let myChart = echarts.init(document.getElementById('myChart'))
      // 绘制图表
      myChart.setOption({
          color:['#C566F6', '#F2675C','#F89B41'],
          series : [
              {
                  name: '访问来源',
                  type: 'pie',
                  radius: '65%',
                  data:[
                      {value:500,name:'理财账户'},
                      {value:250,name:'法币账户'},
                      {value:250,name:'币币账户'}
                  ]
              }
          ]
      })
    }
   },
   mounted(){
     this.drawLine();
   },
   components: {

   }
  }
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  .myAssetWrapper{
    background-color: #fff;
    padding: 10px 10px 0;
    .title{
      color:#333;
      font-weight: 700;
      font-size: 16px;
      margin-right: 35px;
    }
    .my_asset_wrapper{
      padding-bottom: 30px;
      border-bottom: 1px dashed #EEEDED; 
      .my_asset_content{
        display: flex;
        justify-content: flex-start;
        margin-bottom: 10px;
        .coin_sel{
          cursor: default;
          font-size: 14px;
          color: #666;
        }
      }
      .total_content{
        font-size: 14px;
        color: #666;
        .num{
          color:#F5963A;
          font-size: 14px;
        }
      }
    }
    .funds_account_wrapper{
      margin-top: 10px;
      .funds_account_title{
        margin-bottom: 25px;
        .acount_funds{
          font-size: 12px;
          color: #286AFA;
          font-weight: 700;
        }  
      }
      .funds_account_content{
        padding-bottom: 30px;
        border-bottom: 1px dashed #EEEDED; 
        .total_funds{
          color: #666;
          font-size: 14px;
          text-align: center;
          margin-bottom: 10px;
        }
        .money{
          text-align: center;
          font-size: 20px;
          color: #F5963A;
          margin-bottom: 20px;
          .coin_kinds{
            color: #666;
            font-size:14px;
            margin-left: 5px;
          }
        }
        .btn_group{
          text-align: center;
        }
      }
    }
    .trade_account_wrapper{
      margin-top: 10px;
      padding-bottom: 30px;
      .trade_account_content{
        .trade_detail{
          margin-top: 30px;
          display: flex;
          .trade_img{
            width: 40%;
            #myChart{
              margin: 0 auto;
            }
          }
          .detail_info{
            width: 10%;
            padding-top:50px;
            .total_name{
              color: #666;
              font-size: 16px;
              margin-bottom: 5px;
              font-weight: 600;
            }
            .total_funds{
              color:#F5963A;
              font-size: 20px;
              margin-bottom: 5px;
              font-weight: 600;
            }
            .total_unit{
              color: #666;
              font-size: 16px;
              font-weight: 600;
            }
          }
          .trade_list{
            width: 50%;
            padding-left: 120px;
            padding-top: 20px;
            box-sizing: border-box;
            .list_wrapper{
              line-height: 30px;
              margin-bottom: 10px;
              .list_circle{
                display: inline-block;
                width: 12px;
                height: 12px;
                border-radius: 50%;
                margin-right: 5px;
              }
              .list_name{
                color: #666;
                font-size: 14px;
                margin-right: 30px;
              }
              .list_funds{
                color: #666;
                font-size: 14px;
                margin-right: 30px;
              }
              .list_link{
                font-size: 12px;
                color: #666;
                &:hover{
                  color: #286AFA;
                }
              }
            }
          }
        }
      }
    }
    
  }
</style>



