import Vue from 'vue'
import Vuex from 'vuex'
import user from './moudles/user'
import other from './moudles/other'
import getters from './getters'

Vue.use(Vuex)

const store = new Vuex.Store({
  modules:{
    user,other
  },
  getters
})

export default store


