            "symbol":this.depDefult.split('/').join('_'),
          },
          "unique":"abcd"
        }
        this.depRts.tloading = true
        this.socketApi.sendSock(1, recent_trades, this.getConfigResult)


        if(this.isToken){
          /* 24H成交信息 */
          const trade24 = {
            "type":"subscribe.spot_contract_info" , 
            "detail":{"symbol":this.depDefult.split('/').join('_')}, 
            "unique":"abcd"
          }
          this.socketApi.sendSock(1, trade24, this.getConfigResult)

          /* 交易对的账户信息 */
          const usersymbols = {
            "type":"subscribe.single_position", 
            "detail":{
              "token":this.$store.getters.token,
              "symbol":this.depDefult.split('/').join('_'),
              "timestamp":new Date().getTime().toString()
            },
            "unique":"abcd" 
          } 
          this.socketApi.sendSock(1, usersymbols, this.getConfigResult)

          /* 当前委托 */
          const currTrans = {
            "type":"subscribe.order", 
            "detail":{
              "token":this.$store.getters.token,
              "symbol":this.depDefult.split('/').join('_'),
              "timestamp":new Date().getTime().toString()
            } ,
            "unique":"abcd"
          }
          // console.log('currTrans', currTrans)
          this.deles.loading = true
          this.socketApi.sendSock(1, currTrans, this.getConfigResult)

          /* 历史委托 */
          const hisTrans = {
            "type":"subscribe.complete_order", 
            "detail":{
              "token":this.$store.getters.token,
              "symbol":this.depDefult.split('/').join('_'),
              "timestamp":new Date().getTime().toString()
            } ,
            "unique":"abcd"
          }
          this.deles.loading = true
          this.socketApi.sendSock(1, hisTrans, this.getConfigResult)

        }
        
      },

      /* 取消订阅 */
      cancelSubscrip(){
        /* 品种信息数据 */
        const usymbols = {
