          },
          "unique":"abcd"
        }
        this.socketApi.sendSock(1, usymbols, this.getConfigResult)

        /* 深度 */
        const uticks = {
          "type":"unsubscribe.tick" ,
          "detail":{
            "symbol":this.depDefult.split('/').join('_'),
          },
          "unique":"abcd"
        }
        this.socketApi.sendSock(1, uticks, this.getConfigResult)

        /* 实时成交 */
        const urecent_trades = {
          "type":"unsubscribe.market_trade" ,
          "detail":{
            "symbol":this.depDefult.split('/').join('_'),
          },
          "unique":"abcd"
        }
        this.socketApi.sendSock(1, urecent_trades, this.getConfigResult)

        if(this.isToken){
          /* 24H成交信息 */
          const utrade24 = {
            "type":"unsubscribe.spot_contract_info" , 
            "detail":{"symbol":this.depDefult.split('/').join('_')}, 
            "unique":"abcd"
          }
          this.socketApi.sendSock(1, utrade24, this.getConfigResult)

          /* 交易对的账户信息 */
          const usersymbols = {
            "type":"unsubscribe.single_position", 
            "detail":{
              "token":this.$store.getters.token,
              "symbol":this.depDefult.split('/').join('_'),
              "timestamp":new Date().getTime().toString()
            },
            "unique":"abcd" 
          } 
          this.socketApi.sendSock(1, usersymbols, this.getConfigResult)

          /* 当前委托 */
          const ucurrTrans = {
            "type":"unsubscribe.order", 
            "detail":{
              "token":this.$store.getters.token,
              "symbol":this.depDefult.split('/').join('_'),
              "timestamp":new Date().getTime().toString()
            } ,
            "unique":"abcd"
          }
          this.socketApi.sendSock(1, ucurrTrans, this.getConfigResult)

          /* 历史委托 */
          const uhisTrans = {
            "type":"unsubscribe.complete_order", 
            "detail":{
              "token":this.$store.getters.token,
              "symbol":this.depDefult.split('/').join('_'),
