            } ,
            "unique":"abcd"
          }
          this.socketApi.sendSock(1, uhisTrans, this.getConfigResult)

        }
      },

      /* 回调函数 */
      getConfigResult(e){
          // console.log(e)
          if(e.result == 2){
            this.$message.warning('未登录或登录已过期')
            const logoutInfo = {
              "user":this.$store.getters.users,
              "aes":this.$store.getters.token
            }
            this.$store.dispatch('logout', logoutInfo).then(res=>{
                if(res.data.result == 0){
                    this.$router.replace({ path: '/login' });
                }else{
                    this.$message.info(res.data.info)
                }
            })
          }

          // K线信息
          // if(e.type == 'history'){
          //   this.Ks.loading = false
          //   console.log('K线', e)
          //   if(e){
          //     this.Ks.Klines = e
          //     this.Ks.flag = true
          //   }
          // }

          // 品种信息(symbols)
          if(e.type == 'push.display_page'){
            this.symbols.loading = false
            this.symbols.sys_data = e.data
          }

          // 深度
          if(e.type == 'push.tick'){
            this.depRts.depsBuy = e.bids
            this.depRts.depsSell = e.asks
            this.depRts.sloading = false
          }

          // 实时成交
          if(e.type == 'get_recent_trade'){
            // console.log('实时', e)
            this.depRts.tloading = false
            this.depRts.rts = e.data
          }else if(e.type == 'push.market_trade'){
            this.depRts.tloading = false
            // console.log('实时更新', e)
            var depArr = this.depRts.rts
            var newDep = e.data
            depArr.push(newDep)
            if(depArr.length > 30){
              this.depRts.rts = depArr.slice(0,30)
            }else{
              this.depRts.rts = depArr
            }
          }

          // 当前委托
          if(e.type == 'query_open_order'){
            // console.log('当前委托', e)
            this.deles.loading = false
            this.deles.currDeles = e.data
          }else if(e.type == 'order.update'){
            // console.log('当前委托更新', e)
            this.deles.loading = false
            var dataArr = this.deles.currDeles
            var newObj = e.data
            if(dataArr.length == 0){
              if(newObj.order_status == 'n' || newObj.order_status == 'p'){
                dataArr.unshift(newObj)
              }
            }else{
              var delesSame = this.isJudgeDeleSame(dataArr,newObj)
              if(delesSame || delesSame == 0){
                dataArr.splice(delesSame,1)
                if(newObj.order_status == 'n' || newObj.order_status == 'p'){
                  dataArr.push(newObj)
                }
              }else{
                if(newObj.order_status == 'n' || newObj.order_status == 'p'){
                  dataArr.push(newObj)
                }
              }
            }
            this.deles.currDeles = dataArr
          }

          // 历史委托
          if(e.type == 'query_recent_complete_order'){
            // console.log('历史委托', e)
            this.deles.loading = false
            this.deles.hisDeles = e.data
          }else if(e.type == 'complete_order.update'){
            // console.log('历史委托更新', e)
            this.deles.loading = false
            var dataArr = this.deles.hisDeles
            var newObj = e.data
            dataArr.push(newObj)
            this.deles.hisDeles = dataArr
          }

          // 账户信息(account)
          if(e.type == 'position.update'){
            // console.log('账户信息', e)
            var balanceInfo = e.data
            if(balanceInfo.coin == this.trades.base_symbol ){
              this.trades.base_symbol_balance = balanceInfo.balance 
            }else if(balanceInfo.coin == this.trades.target_symbol){
              this.trades.target_symbol_balance = balanceInfo.balance
            }
          }

          /* 24H成交信息 */
          if(e.type == 'push.contract_tick_info'){
            this.Ks.dayInfo = e.data
          }
          
      },

      /*切换品种*/
      operaSelfSymbol(row, column, event){
        // console.log('row',row,'column',column,'event',event)
        if(this.depDefult !== row.symbol){
          this.Ks.flag = false
          this.cancelSubscrip()
          this.depDefult = row.symbol
          setTimeout(()=>{this.Ks.flag = true},200)
          sessionStorage.setItem('symbols',row.symbol)
          this.get_single_rule()
          this._webSocket()
        }
      },

      /* 判断默认的品种 */
      isJudgeDefaultSysbol(){
        var defaultSymbol = sessionStorage.getItem('symbols')
        defaultSymbol?this.depDefult = defaultSymbol:this.depDefult='BTC/USDT'
      },

      /* 判断当前委托是新数据是否与老数据有相同 */
      isJudgeDeleSame(arr,news){
        for(var i=0;i<arr.length;i++){
          if(arr[i].order_id == news.order_id){
            return i
          }
        }
      },

      /* 交易规则 */
      get_single_rule(){
        const data = {"symbol":this.depDefult.split('/').join('_')}
        this.$store.dispatch('get_single_rule', data).then(res => {
          if(res.data.result == 0){
            this.tradeRules = res.data.detail
            // console.log('this.tradeRules', this.tradeRules)
          }
        })
      },

      /* 监听input值 */
      linstenInput(type){
        var tradeVal = this.kTrades;

        var priceRange = this.tradeRules.min_price+'~'+this.tradeRules.max_price;
        var numRange = this.tradeRules.min_volume + '~' + this.tradeRules.max_volume
        switch(type){
          case 'lbp':
            /* 限价买入价 */
            if(Number(this.trades.base_symbol_balance) == 0){
              this.$message.warning('可用余额需要高于0')
            }else{
              if(Number(tradeVal.limitTrades.buyPrice) > Number(this.trades.base_symbol_balance) || Number(tradeVal.limitTrades.buyTrades) > Number(this.trades.base_symbol_balance)){
                this.$message.warning('可用余额不足')
              }else if(Number(tradeVal.limitTrades.buyPrice) < Number(this.tradeRules.min_price) || Number(tradeVal.limitTrades.buyPrice) > Number(this.tradeRules.max_price)){
                this.$message.warning('输入值需在'+priceRange+'范围内')
              }else{
                var val = tradeVal.limitTrades.buyPrice % this.tradeRules.price_tick
                if(val != 0){
                  var newVal = Math.round(Number(tradeVal.limitTrades.buyPrice)/Number(this.tradeRules.price_tick)) * Number(this.tradeRules.price_tick)
                  tradeVal.limitTrades.buyPrice = newVal
                } 
              }
            }
            break;
          case 'lbn':
            /* 限价买入量 */
            if(Number(tradeVal.limitTrades.buyNum) < Number(this.tradeRules.min_volume) || Number(tradeVal.limitTrades.buyNum) > Number(this.tradeRules.max_volume)){
              this.$message.warning('输入值需在'+numRange+'范围内')
            }else{
              var tradeFunds = (Number(tradeVal.limitTrades.buyPrice) * Number(tradeVal.limitTrades.buyNum)).toFixed(8);
              if(tradeFunds > Number(this.trades.base_symbol_balance)){
                this.$message.warning('可用余额不足')
              }
            }
            break;
          case 'lsp':
            /* 限价卖入价 */
            if(Number(this.trades.target_symbol_balance) == 0){
              this.$message.warning('可用余额需要高于0')
            }else{
              if(Number(tradeVal.limitTrades.sellPrice) < Number(this.tradeRules.min_price) || Number(tradeVal.limitTrades.sellPrice) > Number(this.tradeRules.max_price)){
                this.$message.warning('输入值需在'+priceRange+'范围内')
              }else{
                var val = tradeVal.limitTrades.sellPrice % this.tradeRules.price_tick
                if(val != 0){
                  var newVal = Math.round(Number(tradeVal.limitTrades.sellPrice)/Number(this.tradeRules.price_tick)) * Number(this.tradeRules.price_tick)
                  tradeVal.limitTrades.sellPrice = newVal
                } 
              }
            }
            break;
          case 'lsn':
            /* 限价卖出量 */
            if(Number(tradeVal.limitTrades.sellNum) < Number(this.tradeRules.min_volume) || Number(tradeVal.limitTrades.sellNum) > Number(this.tradeRules.max_volume)){
              this.$message.warning('输入值需在'+numRange+'范围内')
            }else{
              var tradeFunds = (Number(tradeVal.limitTrades.sellPrice) * Number(tradeVal.limitTrades.sellNum)).toFixed(8);
              if(tradeFunds > Number(this.trades.target_symbol_balance)){
                this.$message.warning('可用余额不足')
              }
            }
            break;
          default:
            break;
        }
      },

      /* 交易操作 */
      tradeOpera(str){
        switch(str){
          case 'lb':
            if(!this.kTrades.limitTrades.bload){
              if(this.kTrades.limitTrades.buyPrice != '' && this.kTrades.limitTrades.buyNum != ''){
                const data = {
                  "type":"buy" , 
                  "volume":this.kTrades.limitTrades.buyNum, 
                  "price":this.kTrades.limitTrades.buyPrice, 
                  "symbol": this.depDefult.split('/').join('_'), 
                  "unique":"abc"
                }
                this.kTrades.limitTrades.bload = true
                this.$store.dispatch('limit_opera', data).then(res => {
                  if(res.data.result == 0){
                    this.$message.success('操作成功')
                    this.kTrades.limitTrades.bload = false
                    this.kTrades.limitTrades.buyPrice = ''
                    this.kTrades.limitTrades.buyNum = ''
                    // console.log('订单', res)
                  }else{
                    this.kTrades.limitTrades.bload = false
                  }
                }).catch(err => {
                  this.kTrades.limitTrades.bload = false
                })
              }else{
                // this.$message.warning('信息不能为空')
                this.$message({
                  message:'信息不能为空',
                  type:'warning',
                  duration:2000
                })
              }
            }
            break;
          case 'ls':
            if(!this.kTrades.limitTrades.sload){
              const data = {
                "type":"sell" , 
                "volume":this.kTrades.limitTrades.sellNum, 
                "price":this.kTrades.limitTrades.sellPrice, 
                "symbol": this.depDefult.split('/').join('_'), 
                "unique":"abc"
              }
              this.kTrades.limitTrades.sload = true
              this.$store.dispatch('limit_opera', data).then(res => {
                  if(res.data.result == 0){
                    this.$message.success('操作成功')
                    this.kTrades.limitTrades.sload = false
                    this.kTrades.limitTrades.sellPrice = ''
                    this.kTrades.limitTrades.sellNum = ''
                    // console.log('订单', res)
                  }else{
                    this.kTrades.limitTrades.sload = false
                  }
                }).catch(err => {
                  this.kTrades.limitTrades.sload = false
                })
            }
            break;
          case 'mb':
            if(!this.kTrades.marketTrades.bload){
              if(this.kTrades.marketTrades.buyNum == ''){
                this.$message.info('买入量不能为空')
                return false
              }
              const mbs= {"type":"buy" , "volume":this.kTrades.marketTrades.buyNum  , "symbol": this.depDefult.split('/').join('_') , "unique":"abc"}
              this.kTrades.marketTrades.bload = true
              this.$store.dispatch('market_opera', mbs).then(res => {
                if(res.data.result == 0){
                  this.$message.success('操作成功')
                  this.kTrades.marketTrades.bload = false
                  this.kTrades.marketTrades.buyNum = ''
                  // console.log('订单', res)
                }else{
                  this.kTrades.marketTrades.bload = false
                }
              }).catch(err => {
                this.kTrades.marketTrades.bload = false
              })
            }
            break;
          case 'ms':
            if(!this.kTrades.marketTrades.sload){
              if(this.kTrades.marketTrades.sellNum == ''){
                this.$message.info('卖出量不能为空')
                return false
              }
              const mss= {"type":"sell" , "volume":this.kTrades.marketTrades.sellNum  , "symbol": this.depDefult.split('/').join('_') , "unique":"abc"}
              this.kTrades.marketTrades.sload = true
              this.$store.dispatch('market_opera', mss).then(res => {
                if(res.data.result == 0){
                  this.$message.success('操作成功')
                  this.kTrades.marketTrades.sload = false
                  this.kTrades.marketTrades.sellNum = ''
                  // console.log('订单', res)
                }else{
                  this.kTrades.marketTrades.sload = false
                }
              }).catch(err => {
                this.kTrades.marketTrades.sload = false
              })
            }
            break;
          default:
            break;
        }
      },

      /* 撤单 */
      cancelOrder(id){
        this.$confirm('确定撤单吗', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
           const data = {"symbol":this.depDefult.split('/').join('_') , "order_id":id}
           this.$store.dispatch('cancel_order', data).then(res => {
             if(res && res.data.result == 0){
               this.$message.success('撤单成功')
             }
           })
        }).catch(() => {
          this.$message.info('取消操作')
        })
      },

      // // 获取交易订单的数据
      // getTradeOrder(){
