              height: 35px;
              line-height: 35px;
              margin-bottom: 5px;
              text-align: center;
              border: 1px solid $border_active_color;
              color:$dark_font_color;
              a{
                color:$active_font_color;
              }
            }
            .limit_wrapper,.market_wrapper{
              padding:10px;
              box-sizing: border-box;
              display: flex;
              justify-content: space-between;
              .loss_buy{
                width: 50%;
                color: $dark_font_color;
                font-size: 14px;
              }
              .loss_sell{
                width: 50%;
                padding-left:25px;
                box-sizing: border-box;
                color: $dark_font_color;
                font-size: 14px;
              }
            }
          }
        }
      }

      /* 委托列表 */
      .list_wrapper{
        width: 320px;
        min-width: 300px;
        max-width: 340px;
        margin-left:3px;
        .entrust_wrapper{
          width: 100%;
          min-height: 100%;
          height: 600px;
          background-color: $module_bg_color;
          .entrust_content{
            height: 100%;
            .depth_wrpper{
              width: 100%;
              overflow: auto;
              height: 100%;
            }
            .rts_wrpper{
              width: 100%;
              overflow: auto;
              height: 100%;
            }
          }
        }
      }
    }
    .delegion_wrapper{
      min-height: 240px;
      background-color: $module_bg_color;
      width: 100%;
      min-width: 1556px;
      margin-top: 3px;
      // height: 500px;
      // overflow: auto;
      .buy_in{
        padding: 3px 10px;
        background-color: $buy_btn_bg_color;
        font-size: $base_font;
        color: $light_font_color;
        
      }
      .sell_out{
        padding: 3px 10px;
        background-color: $sell_btn_bg_color;
        font-size: $base_font;
        color: $light_font_color;
      }
      .revoke{
        padding: 3px 10px;
        font-size: $base_font;
        color: $light_font_color;
        border: 1px solid $light_font_color;
        cursor: pointer;
      }
    }

