<template>
  <div id="app">
    <router-view v-if="isRouterAlive"/>
    <load></load>
  </div>
</template>

<script>
import load from '@/components/loading/loading'

export default {
  name: 'App',

  provide(){
    return {
      reload: this.reload
    }
  },
  data(){
    return {
      isRouterAlive:true
    }
  },
  methods:{
    reload(){
      this.isRouterAlive = false;
      this.$nextTick(function(){
        this.isRouterAlive = true
      })
    }
  },
  components:{
    load
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    height: 100%;
  }
</style>



