import http from '@/utils/http'
import qs from 'qs'
import CryptoJS from 'crypto-js'
import { getToken, setToken, removeToken } from '@/utils/auth'

function webSign(token, message){
    const key = token.toString() + "123456"
    const hash = CryptoJS.HmacSHA256(message, key);
    return CryptoJS.enc.Base64.stringify(hash);
}

function getSign( timestamp, method, request_path , body , token){
    const message = timestamp + method + request_path + body
    return webSign(token, message)
}

function makeRequest( method , params='' , request_path , token ){
    if(method === 'POST') {
        const timestamp = new Date().getTime() / 1000.0
        var str_data = qs.stringify(params)
        if(str_data.length == 0){
            str_data = ""
        }else{
            str_data = "?" + str_data
        }
        const sign = getSign(timestamp, method,  request_path  , str_data , token.toString())
        const headers = {
            "SC-ACCESS-FROM":"WEB",
            "SC-ACCESS-TIMESTAMP":timestamp,
            "SC-SESSION-AES":token.toString(),
            "SC-ACCESS-SIGN":sign
        }
        return http({
            url:request_path +  str_data,
            method:'post',
            headers: headers
        })
    }else{
        return http({
            url:request_path,
            method:'get',
            params: params
        })
    }
}

// 单条交易规则
export function get_single_rule(data){
    return makeRequest("GET",data,'/api/spot/v1/exchange_single',"")
}

