<template>
  <el-header class="head_warpper">
    <div class="head_content">
      <el-row type="flex" align="middle" class="head_row">
        <el-col :span="4">
          <p class="logo" @click="() => {this.$router.replace('/market')}">LOGO</p>
        </el-col>
        <el-col :span="10" class="tab_content">
          <div class="tab_child" :data="$route.path">
            <a href="javascript:;" @click="() => {this.$router.replace('/home/trade')}" :class="($route.path=='/home/trade' || $route.path.indexOf('/home/trade') != -1)?'a_active':''">{{$t('user.bbjy')}}</a>
          </div>
        </el-col>
        <el-col :span="10" class="user_content">
          <a href="javascript:;" @click="() => {this.$router.replace('/login')}" v-if="!isToken">{{$t('user.login')}}</a>
          <a href="javascript:;" @click="() => {this.$router.replace('/register')}" v-if="!isToken">{{$t('user.register')}}</a>
          <a href="javascript:;" @click="() => {this.$router.replace('/funds')}" v-if="isToken">{{$t('user.asset')}}</a>
          <!-- <a href="javascript:;" @click="() => {this.$router.replace('/order')}" v-if="isToken">订单</a> -->
          <a class="single_spill personal" v-if="isToken">
            <el-popover placement="bottom" >
              <i class="el-icon-user-solid" slot="reference"></i>

              <!-- <div class="cardContent" >
                <p class="cardH" @click="phone_info_dialog = true">修改手机号</p>
                <p class="cardH" @click="email_info_dialog = true">修改邮箱</p>
                <p class="cardH" @click="pwd_info_dialog = true">修改密码</p>
                <p class="cardH" @click="payPwd_info_dialog = true">设置支付密码</p>
              </div> -->
            </el-popover>
          </a>

          <a class="down_app">
            <el-popover placement="right-end" width="200" trigger="hover" >
              <el-card :body-style="{ textAlign: 'center' }">
                <div style="padding: 14px;">
                  <span>{{$t('opera.scan')}}</span>
                </div>
                <el-image style="width: 100px; height: 100px" :src="downLoadImg" fit="fill"></el-image>
              </el-card>
              <i slot="reference" class="el-icon-mobile" style="font-size:16px;"></i>
            </el-popover>
          </a>

          <a class="notice_p">
            <el-popover placement="bottom" width="260" trigger="hover">
              <i class="iconfont icon-notice" slot="reference"></i>
              <div class="cardContent" style="text-align:center;">
                暂未开放，敬请期待
                <!-- <p v-for="notice in notices" :key="notice.index" class="cardH single_spill">{{notice.title}}</p>
                <p class="look_more" @click="()=>{this.$router.push('/noticeList')}">{{$t('opera.more')}}</p> -->
              </div>
            </el-popover>
          </a>

          <!-- <a class="lang_c">
            <el-popover placement="bottom" width="110" trigger="hover">
              <img :src="default_flag || chinaSrc" slot="reference" class="flag" >
              <div class="cardContent" >
                <p class="cardH" v-for="(item, index) in langs" :key="index" @click="language(item.type)">
                  <img :src="item.url" class="flag"> &nbsp;{{item.name}}
                </p>
              </div>
            </el-popover>
          </a> -->
          
          <a href="javascript:;" @click="logout" v-if="isToken">{{$t('user.logout')}}</a>
        </el-col>

      </el-row>
    </div>
  </el-header>
</template>

<script>
  import Mock from '@/utils/mock';
  import chinaSrc from '@/assets/img/china.png'
  import enlishSrc from '@/assets/img/usa.png'

  export default {
    inject:['reload'],

    data() {
      return {
        isToken:Boolean(this.$store.getters.token),
        users:this.$store.getters.users,
        downLoadImg:'http://sct.fricobloc.com/img/download.png',
        notices:Mock.notices.slice(0,5),
        default_flag:'',
        langs:[]
      }
    },
    created(){
      this.isJudgeLang()
      // this.rlangs()
    },
    methods: {
      
      /* lang的默认值 */
      isJudgeLang(){
        var lang = localStorage.getItem('lang')?localStorage.lang:'zh'
        switch(lang){
          case 'zh':
            this.default_flag = chinaSrc
            break;
          case 'en':
            this.default_flag = enlishSrc
            break;
          default:
            break
        }
        this.rlangs()
      },

      /* 语言集 */
      rlangs(){
        var sellang = localStorage.getItem('lang')?localStorage.lang:'zh'
        var langs = [{type:'zh',url:chinaSrc,name:'简体中文'},{type:'en',url:enlishSrc,name:'English'}]
        for(var i=0;i<langs.length ;i++){
          if(langs[i].type == sellang){
            langs.splice(i,1)
          }
        }
        this.langs = langs
      },

      /* lang的选择 */
      language(type){
        localStorage.setItem('lang', type)
        this.$i18n.locale = type;
        this.isJudgeLang()
      },
      /**
       * 退出
       */
      logout(){
        this.$confirm('确定退出吗', '提示', {
          confirmButtonText:'确定',
          cancelButtonText:'取消',
          type:'warning'
        }).then(() => {
          const logoutInfo = {
            "user":this.$store.getters.users,
            "aes":this.$store.getters.token
          }
          this.$store.dispatch('logout', logoutInfo).then(res=>{
              if(res.data.result == 0){
                  this.$router.replace({ path: '/login' });
              }else{
                  this.$message.info(res.data.info)
              }
          })
        }).catch(() => {
          this.$message.info('取消操作')
        })
      }
    }
  }
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/style/base.scss';
  
  .head_warpper{
    width: 100%;
    height: 44px!important;
    background-color: $module_bg_color;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1600;
    .head_content{
      width: 100%;
      height: 100%;
      padding: 0 20px;
      box-sizing: border-box;
      .head_row{
        height: 100%;
        .tab_content{
          height: 44px;
          .tab_child{
            height: 100%;
            display: flex;
            justify-content: flex-start;
            >a{
              line-height:47px;
              padding:0 15px;
              border-bottom:2px solid transparent;
              color: $dark_font_color;
              font-size: $font14;
              margin-right: 25px;
              &:hover{
                color: $light_font_color;
                border-bottom-color: $active_font_color;
              }
              &.a_active{
                color: $light_font_color;
                border-bottom-color: $active_font_color;
              }
            }
          }
        }
        .user_content{
          min-width: 260px;
          height: 100%;
          display: flex;
          justify-content: flex-end;
          line-height: 44px;
          a{
            padding:0 10px;
            color: $dark_font_color;
            font-size: $font14;
            &:hover{
              color: $active_font_color;
            }
          }
          .personal{
            cursor: default;
            color: $dark_font_color;
            margin-right: 3px;
          }
          .down_app{
            margin-right:3px; 
          }
          .notice_p{
            margin-right:3px; 
          }
          .lang_c{
            .flag{
              width: 25px;
              vertical-align: middle;
            }
          }

          // .user_opera{
          //   line-height: 44px;
          //   a{
          //     padding:0 10px;
          //     color: #fff;
          //     font-size: 14px;
          //   }
          //   .personal{
          //     float: left;
          //     max-width: 150px;
          //     cursor: default;
          //   }
          // }
          // .lang_sel{
          //   margin-top:7px;
          // }
        }
        .logo{
          line-height: 44px;
          color: $dark_font_color;
          font-size: $font16;
          text-align: center;
          cursor: pointer;
        }
        

      }
    }
  }
</style>



