<template>
  <div class="load_wrapper" v-if="loading">
    <vue-loading class="load" type="spiningDubbles" style="" color="#5AC1DD" :size="{ width: '100px', height: '100px' }" ></vue-loading>
  </div>
</template>

<script>
  export default {
    name:'loading',
    data() {
      return {
        loading:false
      }
    },
    created(){
      var that = this
      this.bus.$on('loading', function(data){
        that.loading = !!data
      })
    },
    methods: {

    },
    components: {

    }
  }
</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  .load_wrapper{
    position:fixed;top:0;left:0;
    background:rgba(0,0,0,0.7);
    z-index:11111111;width:100%;height:100%;
    .load{
      position:relative;
      top:50%;left:50%;
      margin-top:-50px;
      margin-left:-50px;
    }
  }
</style>



