import context from '@/main.js'
import axios from 'axios';                                                    // 引入axios
import { Message} from 'element-ui';                                          // 引入element的提示框组件
import store from '@/store/index'                                             // 引入请求的vuex模块
import { getToken, removeToken } from '@/utils/auth'                          // 引入登录的token

// 创建axios实例
const service = axios.create({
  baseURL: process.env.BASE_API,                                           // api请求的base_url
  // baseURL: '/api',                                                            // api请求的base_url
  timeout: 5000                                                               // 请求超时时间
})

// request 拦截器
service.interceptors.request.use(
  config => {
    // context.bus.$emit('loading', true)
    return config
  },
  error => {
    return Promise.error(error)
  }
)

// response 拦截器
service.interceptors.response.use(
  response => {
    // setTimeout(()=>{
    //   context.bus.$emit('loading', false)
    // },500)
    if(response.data.result == 0){                                            // 请求成功  返回数据
      return response
    }else if(response.data.result == 2){
      removeToken()
      // localStorage.clear()
      Message({
        message: '未登录或登录已过期',
        type: 'warning',
        duration: 3000
      })
      window.location.reload()
    }else{
      Message({
        message: response.data.info,
        type: 'warning',
        duration: 3000
      })
      setTimeout(() => {return response },3000)
    }
  },
  error => {
    // context.bus.$emit('loading', false)
    Message({
      message:'网络繁忙，请稍后再试',
      type:'error',
      duration:10*1000
    })
    return Promise.reject(error)
  }
)
export default service;



