import router from './router'
import { getToken } from '@/utils/auth'                            // getToken from cookie

const whiteList = ['/login','/register','/market','/home/trade','/reset','/verify','/new','/noticeList','/noticeDetail']     // no redirect whitelist

router.beforeEach((to, from, next) => {
  // chromea
  document.body.scrollTop = 0
  // firefox
  document.documentElement.scrollTop = 0
  // safari
  window.pageYOffset = 0
  if(getToken()){                                 // determine if there has token
    /* has token */
    if(to.path === '/login'){
      next({ path: '/' })
    }else{
      next()
    }
  }else{
    /* has no token */
    if(whiteList.indexOf(to.path) !== -1){      // 在免登录白名单， 直接进入
      next()
    }else{
      next(`/login?redirect=${to.path}`)       // 否则全部重定向到登录页
      // NProgress.done()                         // if current page is login will noe trigger afterEach hook , so manuall handle it
    }
  }
})

router.afterEach(() => {

})



